div.container {
    width: 90%;
	max-width:1000px;
	margin-left: auto;
	margin-right: auto;
	text-align: center;
	margin-top: 80px;
    margin-bottom: 60px;
	display: block;
	font-family: 'AvenirMedium';
	font-size: 13px;
	color: #111;
}

.button{
    display: inline-block;
    background: #333;
    color: #fff;
    padding: 10px 5px;
    margin-bottom: 10px;
	text-decoration: none;
	width: 250px;
}

.red-boarder{
    border-color: red !important;
}

.file-uploader {
    margin-bottom: 40px !important;
}

.file-uploader-label {
    margin-bottom: 10px !important;
}

.form-explanation {
    height: auto !important;
    text-indent: 0;
}

.form-explanation-label {
    padding: 5px 0;
}

.form-multiple-choise-wrapper {
    padding: 0 5px;
}

.form-multiple-choise-wrapper__items {
    display: flex;
    flex-wrap: wrap;
    min-width: calc(100%);
}

.form-multiple-choise-wrapper__item {
	display: inline-block;
	margin-bottom: 5px;
}

.form-multiple-choise-wrapper__item > [type="checkbox"]:not(:checked) + label:before,
.form-multiple-choise-wrapper__item > [type="checkbox"]:checked + label:before {
    display: none;
    content: none;
}

.form-multiple-choise-wrapper__item > [type="checkbox"]:not(:checked) + label:after,
.form-multiple-choise-wrapper__item > [type="checkbox"]:checked + label:after {
    display: none;
    content: none;
}

.form-multiple-choise-wrapper__item > [type="checkbox"]:checked + label, 
.form-multiple-choise-wrapper__item > [type="checkbox"]:not(:checked) + label {
    padding-left: 5px;
}


.form-multiple-choise-wrapper__item > [type="checkbox"] {
    -webkit-appearance: checkbox !important;
    position: relative;
    left: auto;
}


.calendar{
	text-align: center;
	margin-left: auto;
	margin-right: auto;
	font-family: 'AvenirMedium';
	font-size: 15px;
	width: 90%;
	
	margin-top: 80px;
}

.cal, .charts{
	width: 100%;
	overflow: hidden;
}

.cal{
	max-width: 450px;
	margin-left: auto;
	margin-right: auto;
}

.cal_arrow{
	display: inline-block;
	width: 8px;
	vertical-align: middle;
	cursor: pointer;
}

.cal_arrow:hover{
	opacity: .3;
}

.c_dates{
	display: inline-block;
	vertical-align: middle;
	margin-left: 20px;
	margin-right: 20px;
}

.cal_arrows{
	text-align: center;
	margin-left: auto;
	width: 100%;
	margin-right: auto;
	margin-bottom: 65px;
	font-family: 'AvenirMedium';
	font-size: 15px;
}

.cal_line{
	width: 100%;
	max-width: 435px;
	margin-left: auto;
	margin-right: auto;
	margin-top: 30px;
	margin-bottom: 60px;
	height: 1px;
	background: #666;
}

.cal_headers1{
	display: flex;
	font-size: 15px;
	flex-wrap: wrap;
	height: 40px;
	justify-content: space-between;
	position: relative;
}

.cal_headers{
	display: flex;
	font-size: 15px;
	flex-wrap: wrap;
	height: 60px;
	justify-content: space-between;
	position: relative;
}

.cal_headers_narrow{
	display: flex;
	font-size: 15px;
	flex-wrap: wrap;
	height: 35px;
	justify-content: space-between;
	position: relative;
}

.cal_blocks{
	width: 19%;
	color: #808080;
	font-size: 15px;
}

.cal_fills{
	width: 85%;
	height: 22px;
	position: relative;
	margin-left: auto;
	margin-right: auto;
	border: 1px solid #888;
		cursor: pointer;

}

.c_actives{
	width: 85% !important;
	height: 12px !important;
	opacity: 1 !important;
}

.rights_cal{
    transform: rotate(180deg);

}

.c_green{
	position: absolute;
	left: 50%;
	top: 50%;
	width: 70%;
	opacity: 0;
	transform: translate(-50%, -50%);
	background: #44b07b;
	height: 10px;
		-webkit-transition:all .2s ease-in-out;
	-moz-transition:all .2s ease-in-out;  
    -o-transition:all .2s ease-in-out;
}

.cal_times{
	position: absolute;
	margin-left: -150px;
	margin-top: 45px;
	text-align: right;
	width: 130px;
	font-size: 13px;
	
}

.c_time{
	margin-bottom:42px;
}

.c_time_narrow{
	margin-bottom:17px;
}

.cal_dates{
	width:20%;
	text-align: center;
}


.cal__title {
    position: absolute; top: 0px; left: 0px; z-index: 100; opacity: 1; display: block; visibility: visible;
}

.cal__time {
    margin-bottom: 20px; width: 100%; text-align: left; margin-left: 2px; margin-top: 10px; font-size: 13px
}

.cal__slide {
    position: absolute; top: 0px; left: 0px; z-index: 99; visibility: hidden;
}

.cal__sentinel {
    position: static; top: 0px; left: 0px; z-index: 100; opacity: 1; display: block; visibility: hidden;
}

.title-description {
	margin-top: 5px !important;
	margin-left: 0;
	font-style: italic;
}

.b_texts .alert {
	text-decoration: underline;
} 

.b_texts .description {
	padding-bottom: 10px;
} 

.property-payment {
	font-size: 10pt; 
	font-family: Verdana, arial, Helvetica, sans-serif; 
	width: 100%; 
	border-collapse: collapse; 
	border: 1px solid black
}

.narrow-margin-bottom {
	margin-bottom: 5px;
}


.map_header{
	top: 0;
	padding-top: 110px;
	width: 100%;
	padding-bottom: 40px;
	text-align: center;
	font-family: 'BigCaslon-Medium';
    font-size: 24px;
	z-index: 160;
	color: #4D4E4E;
	position: fixed;
	background: white;
}

.map_holder{
	position: relative;
	width: 100%;
	z-index: 3;
}

.map_btn{
	position: fixed;
	left: 20px;
	top: 215px;
	width: 95px;
	text-align: center;
	z-index: 100000;
	padding-top: 3px;
	padding-bottom: 3px;
	color: white;
	cursor: pointer;
	background: #44b07b;
	border: 2px solid #44b07b;
    font-family: "Gilroy-SemiBold";
	font-size: 13px;
	
}

.map_btn:hover{
	background: #111;
	border: 2px solid #111;
}

.blue_text {
   color: blue;
}

.c_text {
	margin-top: 25px;
	text-align: left;
}

.footer_alert {
	display: flex;
	justify-content: flex-end;
}
.footer_alert .item{
	flex: 0 1 30%;
	text-align: left;
}
@media screen and (max-width:1025px) {
	.footer_alert {
		justify-content: flex-start;
	}
	.footer_alert .item{
		flex: 1 1 100%;
	}
}


/* For dashboard */
.offer_action_btn {
	padding-top: 15px;
	height: 35px;
	width: 130px;
	cursor: pointer;
	background-color: #44b07b;
	color: white;
}

.offer_block {
	border-bottom: 1px solid black;
}

.offer_container {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	padding: 15px 0px;
}

.offer_title {
	padding: 25px 0px;
}

.offer_title .offer_no {
	font-family: 'BigCaslon-Medium';
	font-size: 24px;
    line-height: 30px;
}

.offer_item {
	padding: 0px 15px;	
	flex-shrink: 2;
}

.hiw_block_front {
	width: 49.5%;
}

.hiw_section_front {
	padding-top: 15px;
	min-height: 80vh;
}

.hiw_blocks_front {
	padding-top: 50px !important;
	margin-top: 25px !important;
}

@media screen and (max-width:500px) {
	.offer_action_btn {
		width: 100%;
		text-align: center;
	}

	.hiw_block_front {
		width: 100%;
	}

	.offer_item {
		text-align: left !important;
		flex-basis: 500px;
		padding-top: 5px;
		padding-bottom: 15px;
		padding-left: 0px;
		padding-right: 0px;
	}

	.offer_block {
		margin-bottom: 15px;
	}

	.offer_block:last-child {
		border-bottom: none;
	}

	.hiw_holder {
		padding-bottom: 15px;
	}
}
