.react-select-failed__indicator {
    padding: 0px 3px !important;
}

.react-select-failed__value-container
{
    padding: 0 5px !important;
}

.react-select-failed__indicator-separator{
    margin-top: 1px !important;
    margin-bottom: 1px !important;
}

.react-select-failed__control
{
    border-radius: 0px !important;
    min-height: 27px !important;
    margin-bottom: 5px;
    border: 1px solid red !important;
    animation: input_shake 0.2s ease-in-out 0s 2;
}

.react-select-failed__control--is-focused
{
    border: 1px solid red !important;
    box-shadow: 0 0 0 0 red !important;
}

.react-select-failed__control--menu-is-open
{
    border: 1px solid red !important;
    box-shadow: 0 0 0 0 red !important;
}
